import { Component, OnInit } from '@angular/core';
import { Momento } from '../models/momento';
import { User } from '../models/user';
import { MomentoService } from '../services/momento.service';
import { LocalStorageService }  from '../services/local-storage.service';
import { HostService } from '../services/host.service'
import { UserService } from '../services/user.service';
import { Router } from '@angular/router';




@Component({
	selector: 'app-timeline-cmp',
	templateUrl: 'timeline.component.html',
	styleUrls: ['timeline.component.css']
})


export class TimelineComponent implements OnInit{

	public momentos: Momento[];
	public userTimeLine: User;
	public urlUserFundo: string;

	constructor(private momentoService: MomentoService, private localStorageService: LocalStorageService,
		private hostService: HostService,  private userService: UserService,  private router: Router){}

	getLiClass(i: any){	
		if(i % 2 == 0)
			return "timeline-inverted";
		else 
			return "";
	}
	
	ngOnInit(){

		this.userService.getUserByEmail(localStorage.getItem("currentEmailUserEdit")).subscribe(
			userResponse => {
				this.userTimeLine = JSON.parse(JSON.parse(JSON.stringify(userResponse))._body);
				//console.log(this.userTimeLine);
				this.urlUserFundo = this.getUrlFundo(this.userTimeLine);
				console.log("urlfundo:"+this.urlUserFundo);
				if(this.urlUserFundo != "http://ykssistemas.dyndns.org:50001/imersa/null")
					document.body.style.backgroundImage = "url("+this.urlUserFundo+")";
				else
					document.body.style.backgroundImage = "url('../assets/img/login-imersa-22.png')";
				document.body.style.backgroundSize = "100vw 960px";

				this.momentoService.getMomentosByUser(this.userTimeLine).subscribe(
					momentosResponse => {
						this.momentos = JSON.parse(JSON.parse(JSON.stringify(momentosResponse))._body);
						console.log(this.momentos);

					}, error=> console.log(error)

				); 



			}, error => console.log(error)
		);

	}

		getUrlFotoMomento(momento: Momento){

			if(momento)
				if(momento)
				{
					/*console.log("url"+this.hostService.host + this.momentoAtual.urlFoto);*/
					return this.hostService.host + momento.urlFoto;
				}

				return "../assets/img/login-imersa.png";

		}

		goToResumoGallery(momento: Momento){
			localStorage.setItem("currentIdMomentoToResumoGallery", momento.momentoId.toString());
			localStorage.setItem("currentEmailUserEdit", this.userTimeLine.email);
			this.router.navigate(['/app-resumo-galeria']);
		}

		goToListaDepoimentos(){
      		this.router.navigate(['/app-lista-depoimento']);
  		}

  		goToCapacete(){
      		this.router.navigate(['/app-capacete']);
  		}

  		goToMapa(){
      		this.router.navigate(['/app-mapa']);
  		}

		navegarTabelaMemorias(){
			this.router.navigate(['/tables/datatables.net']);
		}

		iAdmin(): Boolean{
			let isAdmin = localStorage.getItem("currentIsAdmin");

			if(isAdmin == 'true')
				return true;

			return false;
		}

		getUrlFundo(user: User){
    		return this.hostService.host + user.urlfundo;
 		}

	}



